let inputWords = [];
const initWord = 'Please fill the word split with comma(,) e.g. "a,b,c,d"';
const buttonSpinId = "button-spin";
const buttonEditId = "button-edit";

// for test
// document.getElementById("words").value = inputWords.join(",");

// elements
const buttonSpeak = document.getElementById("button-speak");
const formWords = document.getElementById("form-words");
const textAreaInput = document.getElementById("words");
const displayedWord = document.getElementById("display-word");
const displayWordCountId = document.getElementById("display-word-count");

// api speak
const msg = new SpeechSynthesisUtterance();

msg.onstart = () => {
  buttonSpeak.setAttribute("disabled", true);
  document.getElementById(buttonEditId)?.setAttribute("disabled", true);
  document.getElementById(buttonSpinId)?.setAttribute("disabled", true);
};
msg.onend = () => {
  buttonSpeak.removeAttribute("disabled");
  document.getElementById(buttonEditId)?.removeAttribute("disabled");

  if (inputWords.length !== 0) {
    document.getElementById(buttonSpinId)?.removeAttribute("disabled");
  }
};

const speak = () => {
  msg.text = displayedWord.innerText;
  window.speechSynthesis.speak(msg);
};

// Start at 0
const randomNumberInRange = (max) => {
  return Math.floor(Math.random() * max);
};

const getWordByRandom = (words) => {
  return words[randomNumberInRange(words.length)];
};

const setFormToDisabled = (formElement) => {
  formElement
    .querySelectorAll("input, textarea, button")
    .forEach((inputElement) => {
      inputElement.setAttribute("readonly", true);
      inputElement.setAttribute("disabled", true);
    });
};

const setFormToEnable = (formElement) => {
  formElement
    .querySelectorAll("input, textarea, button")
    .forEach((inputElement) => {
      inputElement.removeAttribute("readonly");
      inputElement.removeAttribute("disabled");
    });
};

const handleClickSpin = () => {
  if (buttonSpeak.disabled) {
    return;
  }

  inputWords = textAreaInput.value ? textAreaInput.value.split(",") : [];

  if (inputWords && inputWords.length > 0) {
    const randomedWord = getWordByRandom(inputWords);

    inputWords = inputWords.filter((word) => word !== randomedWord);
    textAreaInput.value = inputWords;
    displayedWord.innerText = randomedWord;
    displayWordCountId.innerHTML = inputWords.length || 0;

    speak();
  }
};

const handleClickEdit = () => {
  // setFormToEnable(formWords);
  displayedWord.innerText = initWord;
  formWords.removeAttribute("style");

  document.getElementById(buttonSpinId).remove();
  document.getElementById(buttonEditId).remove();
};

const createButtonSpin = () => {
  const buttonSpin = document.createElement("button");
  buttonSpin.innerText = "Spin";
  buttonSpin.type = "button";
  buttonSpin.id = buttonSpinId;

  buttonSpin.addEventListener("click", (e) => {
    e.target.setAttribute("disabled", true);
    handleClickSpin();
    e.target.removeAttribute("disabled");
  });

  document.body.appendChild(buttonSpin);
};

const createButtonEdit = () => {
  const buttonEdit = document.createElement("button");
  buttonEdit.innerText = "Back to Edit";
  buttonEdit.type = "button";
  buttonEdit.id = buttonEditId;

  buttonEdit.addEventListener("click", (e) => {
    handleClickEdit();
  });

  document.body.appendChild(buttonEdit);
};

formWords.addEventListener("submit", (e) => {
  e.preventDefault();
  // setFormToDisabled(e.target);
  e.target.style.display = "none";
  createButtonSpin();
  createButtonEdit();
  displayWordCountId.innerHTML = textAreaInput.value
    ? textAreaInput.value.split(",").length
    : 0;
});

buttonSpeak.addEventListener("click", () => {
  speak();
});

displayedWord.innerText = initWord;
